@import "../../../styles/root";

.home {
    position: relative;
    top: $height-header;
    // font-family: 'Sunflower', sans-serif;
    height: 743px;
    display: flex;
    align-items: center;
    justify-content: center;

    .content {
        width: 700px;
        height: 500px;
        display: flex;
        flex-direction: column;
        padding: 10px 15px;
        align-items: center;
        justify-content: center;
        background-color: rgba(255, 255, 255, 0.78);
        border-radius: 5px;

        .welcome {
            display: block;
            font-size: 2.5rem;
            margin: 0 0 10px;
            text-align: center;
            line-height: 5rem;
            font-weight: 300;
            @include linerText(45deg, #06619c, #da0214);
        }

        .userName {
            display: block;
            font-size: 3.5rem;
            margin: 0 0 10px;
            line-height: 5rem;
            text-align: center;
            font-weight: 600;
            text-transform: uppercase;
        }

        .introductionUser {
            display: block;
            padding: 0 75px;
            font-size: 1.6rem;
            margin: 0 0 10px;
            text-align: center;
            line-height: 1.8rem;
            @include linerText(45deg, #e70202, #011e33);
        }

        .box {
            padding-top: 50px;
            display: inline-block;
            width: 200px;
            margin: 8px;
        }
    }
}
