@import "../../../styles/root";

.ourTeam {
    font-family: "roboto", sans-serif;
    width: 100%;
    height: 830px;
    box-sizing: border-box;
    padding: 64px 48px;
    color: $text-color;

    .title {
        text-align: center;
        font-size: 32px;
        font-weight: 700;
        color: $text-color;
    }

    .listMember {
        width: 100%;
        height: 600px;
        display: flex;
        align-items: center;
        justify-content: center;

        .memberCard {
            width: 325px;
            height: 535px;
            margin: 20px;
            border: 2px solid #f4f6f8;
            display: flex;
            flex-direction: column;
            border-radius: 10px;
            // box-shadow: 0px 0px 20px rgb(145, 158, 167);
            box-shadow: 0px 0px 20px rgb(212, 228, 231);
            background-color: $white-color !important;

            .cardImg {
                width: 100%;
                height: 100%;

                img {
                    border-top-left-radius: 10px;
                    border-top-right-radius: 10px;
                    width: inherit;
                    height: inherit;
                }
            }

            .cardContent {
                box-sizing: border-box;
                width: 100%;
                height: calc(100% - 64%);
                padding: 45px 32px 32px;

                .name {
                    display: block;
                    margin: 5px 0 0;
                    width: 100%;
                    line-height: 25px;
                    text-align: center;
                    color: black;
                    font-size: 20px;
                    font-weight: 700;
                    cursor: pointer;
                    text-decoration: none;
                }

                .job {
                    margin: 10px 0;
                    width: 100%;
                    line-height: 25px;
                    text-align: center;
                    box-sizing: border-box;
                    font-size: 16px;
                }

                .listIconsSocial {
                    text-align: center;
                    font-size: 19px;
                    .icon {
                        padding: 0 10px;
                        cursor: pointer;
                    }
                    a {
                        color: $text-color;
                    }
                }
            }
        }
    }
}

